html {
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

:root {
  --main-accent: #060b35;
  --blue-accent: #00acdc;
  --white-accent: #fff;
  --secondary-accent: #616070;
  --colomn-count: 1;
}

.card {
  background-color: var(--white-accent);
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 408px;
  margin: auto;
  transition: transform .3s;
}

.card__goods {
  width: 160px;
  height: 134px;
  margin: 32px auto;
  display: block;
}

.card__info {
  color: var(--main-accent);
  width: 166px;
  margin: 40px auto 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__info:hover {
  color: #34568b;
  font-family: Roboto, Helvetica, sans-serif;
}

.card__code {
  color: var(--secondary-accent);
  margin: 0;
  padding-top: 4px;
  padding-bottom: 16px;
  padding-left: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card-chosen {
  justify-content: space-between;
  align-items: flex-end;
  margin: 0;
  padding-bottom: 24px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
}

.card-reviews {
  color: var(--main-accent);
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__buy {
  vertical-align: baseline;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding-bottom: 16px;
  padding-left: 16px;
  padding-right: 16px;
  font-size: 100%;
  display: flex;
}

.card__price {
  color: var(--secondary-accent);
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__sum {
  color: var(--main-accent);
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: var(--white-accent);
  background-color: var(--blue-accent);
  text-transform: uppercase;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  margin-left: 16px;
  margin-right: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  border-color: var(--blue-accent);
  background-color: var(--white-accent);
  color: var(--blue-accent);
  font-family: Roboto, Helvetica, sans-serif;
}

.card:hover {
  transition: transform .3s;
  transform: scale(1.2);
}

.main {
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding-top: 50px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

